var staticRenderFns = [function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"columns body"},[_c('div',{staticClass:"column is-2"}),_c('div',{staticClass:"column"},[_c('div',{staticClass:"columns"},[_c('div',{staticClass:"column"},[_c('h1',{staticClass:"title"},[_vm._v("เพราะอาหารทำให้ฉันได้เปิดโลก")]),_vm._v("\n          โดย : มณีจันทร์ สว่างวงศ์\n          "),_c('br'),_c('br'),_vm._v("\n          เมนูอาหาร : น้ำพริกกะปิ\n          "),_c('br'),_c('br'),_vm._v("\n          ขึ้นชื่อว่าน้ำพริกกะปิ หลายคนอาจจะรู้สึกว่าเป็นอาหารที่ทุกบ้านทำกันได้ง่ายมากๆ และยิ่งในกรุงเทพมหานครด้วยแล้วเราจะพบเห็นอาหารชนิดนี้ได้ตามร้านข้าวแกงแดงร้อนเกือบทุกร้านเลยล่ะครับ แต่วันนี้เราจะพาคุณลองมาเปิดอีกประสบการณ์ในการรับประทานอาหารผ่านมุมมองของคุณยาย “มณีจันทร์ สว่างวงศ์” คุณยายที่ย้ายภูมิลำเนามาจากจังหวัดนครศรีธรรมราช เพื่อมาอยู่กับหลานๆในกรุงเทพมหานคร กับเรื่องราวที่น้ำพริกถ้วยนี้ทำให้คุณยายได้เปิดโลกทัศน์ใหม่ๆเลยครับ\n          "),_c('br'),_c('br'),_vm._v("\n          คุณยายมณีจันทร์เริ่มเข้าสู่กรุงเทพฯเมื่อ 5 ปีที่แล้ว คุณยายยังไม่รู้จักเพื่อนบ้านคนอื่นๆสักเท่าไหร่ ในช่วงแรกคุณยายจึงใช้เวลาว่างที่อยู่บ้าน คอยปลูกผักในรูปแบบ “รั้วกินได้” เพื่อคลายเหงา และนำผักและวัตถุที่สรรหาได้มาประกอบอาหารให้หลานๆทานกัน ซึ่งสิ่งสำคัญที่คุณยายใส่ใจมากที่สุดในการทำอาหารให้หลานนั่นก็คือ “วัตถุดิบ” ในท้องถิ่นที่คุณยายมักจะชอบตุนเอาไว้เสมอเมื่อได้ไปบ้านเกิด\n          "),_c('br'),_c('br'),_vm._v("\n          จนกระทั่งวันหนึ่งที่คุณยายมีโอกาสได้ไปทำบุญที่วัดละแวกบ้าน คุณยายจึงตั้งใจทำอาหารซึ่งหนึ่งในนั้นก็คือ “น้ำพริกกะปิ” คุณยายได้เตรียมวัตถุดิบที่สำคัญคือ “กะปิ” ที่คุณยายนำมาจากบ้านเกิด เนื่องจากมีรสชาติที่เค็มน้อยกว่าและเป็นกะปิที่ใช้สำหรับตำน้ำพริกโดยเฉพาะ จึงทำให้ได้รสชาติที่ตรงต่อความต้องการ และยังได้เก็บผักริมรั้วที่ปลูกเอาไว้ไปด้วย เพื่อนำทั้งหมดไปถวายพระ\n          "),_c('br'),_c('br'),_vm._v("\n          แต่เมื่อไปวัดและถวายพระเสร็จสิ้น ก็ถึงคราวที่เพื่อนบ้านได้ชิมรสชาติที่คุณยายทำมา ซึ่งพอทุกคนได้ชิมรสชาตินั้นแล้วต่างเป็นที่ถูกอกถูกใจ ชื่นชอบกันเป็นอย่างมาก จนมีคนตะโกนกลางวัดว่า “น้ำพริกกะปิถ้วยนี้ ใครเป็นคนตำ ฝีมืออร่อยมากๆ” ซึ่งนั่นทำให้คุณยายเป็นที่รู้จักมากขึ้นและเริ่มมีเพื่อนบ้านมากขึ้น ซึ่งพอมีเพื่อนบ้านมากขึ้นยายก็ไม่พลาดที่จะทำน้ำพริกกะปิไปแจกจ่ายและแบ่งมาให้หลานๆได้ทานกันทุกอาทิตย์\n          "),_c('br'),_c('br'),_vm._v("\n          คุณยายยังเล่าให้ฟังด้วยว่า ทุกครั้งที่คุณยายตำน้ำพริกจะมีความสุขมากๆ เพราะเนื่องด้วยน้ำพริกของคุณยายเป็นน้ำพริกที่ใช้วัตถุดิบจากบ้านของคุณยายโดยเฉพาะ และคุณยายก็ได้ใช้เวลาว่างในการแบ่งปันของอร่อยให้กับหลายๆ คนที่เปรียบเสมือนเป็นหลานอีกคนของยายเช่นกัน\n          "),_c('br'),_c('br'),_vm._v("\n          ซึ่งสุดท้ายแล้ว คุณยายได้บอกว่า “อาหารที่ยายทำ มันอาจจะไม่ได้เงินมาก แต่ยายแค่อยากใช้เวลาตรงนี้ทำในสิ่งที่ยายรัก และส่งต่อพลังงานแห่งความสุขไปให้กับทุกๆ คนที่ได้ทานอาหารของยายเช่นกัน” แค่นี้ก็เพียงพอสำหรับคุณยายมณีจันทร์แล้ว...\n          "),_c('br'),_c('br'),_vm._v("\n          เรื่องโดย : กฤตนัน ดิษฐบรรจง\n        ")])])]),_c('div',{staticClass:"column is-2"})])}]

